<template>
  <div class="columns" style="height: 100vh; background-color: whitesmoke">
    <div class="column is-2" style="background-color: whitesmoke;">
      <AdminMenu />
    </div>
    <div class="column" style="background-color: whitesmoke;">
      <section class="hero is-fullheight">
        <div class="hero-body">
          <div class="container">
            <h1 class="title">SETUP SCHEDULES</h1>
            <v-calendar
              is-expanded
              v-model="date"
              :attributes="attributes"
              @dayclick="modalUp"
              :timezone="timezone"
            />
            <br />
            <div class="modal" :class="{ 'is-active': isActive }">
              <div class="modal-background"></div>
              <div class="modal-content">
                <div class="card">
                  <div class="card-content">
                    <div class="content has-text-centered">
                      <div class="control block">
                        <label class="label">Prefix:</label>
                        <input
                          class="input"
                          type="text"
                          maxlength="10"
                          v-model="prefix"
                          style="width: 33%;"
                          placeholder="e.g. ROOM305"
                        />
                      </div>
                      <label class="label">Time start:</label>
                      <v-date-picker
                        v-model="timeStart"
                        mode="time"
                        :timezone="timezone"
                        class="block"
                      />
                      <br />
                      <label class="label">Time end:</label>
                      <v-date-picker
                        v-model="timeEnd"
                        mode="time"
                        :timezone="timezone"
                        class="block"
                      />
                      <div class="control block">
                        <label class="label">Appointment limit:</label>
                        <input
                          class="input"
                          type="number"
                          v-model="appointmentLimits"
                          style="width: 33%;"
                        />
                      </div>
                      <div class="block">
                        <button
                          class="button is-primary"
                          type="button"
                          @click="addSched"
                          :disabled="prefix == ''"
                        >Confirm</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <button class="modal-close is-large" aria-label="close" @click="modalClose"></button>
            </div>
            <br />
            <div
              class="notification is-success has-text-centered"
              v-if="uploadSchedSuccess"
            >Schedules Added/Updated!</div>

            <h1 class="subtitle">Ongoing Schedules:</h1>
            <div
              class="columns is-gapless is-multiline"
              v-if="Object.keys(daysIndexed).length !== 0"
            >
              <div class="column" v-for="(schedules, index) in daysIndexed" :key="schedules.id">
                <div class="block card">
                  <div class="card-content">
                    <div class="content">
                      <p class="subtitle has-text-black has-text-left">Schedule No. {{ index + 1 }}</p>
                      <p class="has-text-black">{{ new Date(schedules.date).toDateString() }}</p>
                      <p class="has-text-black">Prefix: {{ schedules.prefix }}</p>
                      <p class="has-text-black">{{ schedules.timeStart }} - {{ schedules.timeEnd }}</p>
                      <p class="has-text-black">Appointment limit: {{ schedules.appointmentLimit }}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="notification is-warning" v-else>
              <h1 class="title is-5">No ongoing schedules.</h1>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import store from "../../store";
import AdminMenu from "../../components/AdminMenu.vue"

export default {
  name: "Scheduler",
  components: {
    AdminMenu
  },
  data() {
    return {
      alias: store.state.alias,
      profileImg: store.state.profileImg,
      userID: "",
      date: new Date(),
      timeStart: '',
      timeEnd: '',
      timezone: "",
      prefix: '',
      days: [],
      appointmentLimits: 10,
      checkServer: null,
      isActive: false,
      uploadSchedSuccess: false
    };
  },
  async mounted() {
    await axios
      .get("/session/admin")
      .then((response) => (this.days = response.data.schedule));
    await axios
      .get("/session/admin")
      .then((response) => (this.userID = response.data._id));
  },
  computed: {
    dates() {
      return this.days.map((day) => new Date(day.date).toDateString());
    },
    attributes() {
      return this.dates.map((date) => ({
        highlight: true,
        dates: date,
      }));
    },
    daysIndexed() {
      if (this.days !== '') {
        return this.days.sort((a, b) => {
          return new Date(a.date).getTime() - new Date(b.date).getTime()
        }).filter(x => { return new Date(x.date).getTime() > new Date().getTime() })
      }

    }
  },
  methods: {
    async onDayClick(day) {
      await this.days.push({
        id: day.id,
        date: day.date,
        timeStart: this.timeStart.toLocaleTimeString(),
        timeEnd: this.timeEnd.toLocaleTimeString(),
        appointmentLimit: this.appointmentLimits,
        prefix: this.prefix
      });

      await axios.put(`/api/admin/${this.userID}`, {
        schedule: this.days,
      });
      await axios.put("/session/admin", {
        schedule: this.days,
      });
      await axios
        .get("/session/admin")
        .then((response) => (this.days = response.data.schedule));

      this.uploadSchedSuccess = true
    },
    async modalUp(day) {
      this.isActive = true;
      const idx = this.days.findIndex((d) => d.id === day.id);
      if (await idx >= 0) {
        this.days.splice(idx, 1);
        this.isActive = false;
        await axios.put(`/api/admin/${this.userID}`, {
          schedule: this.days,
        });
        await axios.put("/session/admin", {
          schedule: this.days,
        });
        await axios
          .get("/session/admin")
          .then((response) => (this.days = response.data.schedule));

        this.uploadSchedSuccess = true
      }
      this.day = day;
      this.timeStart = day.date;
      this.timeEnd = day.date;
    },
    addSched(day) {
      day = this.day;
      this.onDayClick(day);
      this.isActive = !this.isActive;
    },
    modalClose() {
      this.isActive = !this.isActive;
    },
  },
};
</script>
<style scoped>
.hero {
  background-color: whitesmoke;
}
</style>